import React, { useEffect, useState } from 'react';
import { Button, Container, Row, Col, Image } from 'react-bootstrap';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let CBox = styled.div`
  padding : 20px;
`;
let CTitle = styled.h4`
  font-size : 25px;
  color : ${ props=>props.COLOR}
`;

function Detail(props) {

  let [alert, chgAlert] = useState(true);
  let history = useHistory();
  let {id} = useParams();

    useEffect(()=>{
      // mount, update될 때 실행 됨.
      console.log("useEffect execute.")
      let timer = setTimeout(()=>{
        chgAlert(false)
      }, 2000);

      // unmount 될 때
      // return function() {   };
      // return ()=> {};
      return () => { clearTimeout(timer) }
    }, []);

    let viewShoes = props.shoes.find(function(item) {
        return item.id == id;
    });

    return (
      <Container>
        <CBox>
          <CTitle className="RED">Detail Page.</CTitle>
        </CBox>
        {
          alert === true
          ?
          <div className="my-alert2">
            <p>재고가 얼마 남지  않았습니다.</p>
          </div>
          :
          null
        }
        <Row>
          <Col><Image src={viewShoes.imageurl} width="100%" /></Col>
          <Col>
            <h4 className="pt-5">{viewShoes.title}</h4>
            <p>{viewShoes.contents}</p>
            <p>{viewShoes.price}</p>
            <Button className="btn btn-danger">Order</Button>
            <Button className="btn btn-danger" onClick={()=>{history.goBack()}}>Back</Button>
          </Col>
        </Row>
      </Container>
    )
}

export default Detail;