/* eslint-disable */

import React, { useState } from 'react';
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Jumbotron, Container, Row, Col, Image, Card } from 'react-bootstrap';
import { Route, Link, Switch } from 'react-router-dom';
import './App.css';
import Shoes from './data.js';
import Detail from './Detail';
import axios from 'axios';

function App() {
  let [shoes, chgShoes] = useState(Shoes);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
      <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link as={Link} to="/">Home</Nav.Link>
          <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
          <NavDropdown title="Dropdown" id="basic-nav-dropdown">
            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
          </NavDropdown>
        </Nav>
        <Form inline>
          <FormControl type="text" placeholder="Search" className="mr-sm-2" />
          <Button variant="outline-success">Search</Button>
        </Form>
      </Navbar.Collapse>
    </Navbar>

    <Switch>
      <Route exact path="/">
        <div>
          <MainJumbo />
          <Container>
            <Row>
              {shoes.map((shoe, i) => {
                return (<Col>
                  <ShoeCard shoe={shoe} idx={i} key={i} />
                </Col>)
              })}
            </Row>
            <Button className="btn btn-primary" onClick={() => {
              axios.get("https://codingapple1.github.io/shop/data2.json")
              .then((result)=>{ 
                console.log(result.data);
                chgShoes([...shoes, ...result.data]);
              })
              .catch((ex)=>{
                console.log(ex)
              }) 
              }}>more...</Button>
          </Container>
        </div>
      </Route>
      <Route path="/detail/:id">
        <div><Detail shoes={shoes}/></div>
      </Route>
      <Route path="/:id">NaN</Route>
    </Switch>
    </div>
  );
}















function MainJumbo() {
  return (
    <Jumbotron className="background">
      <h1>20% Seasons off!!!</h1>
      <p>This is a simple hero unit, a simple jumbotron-style component for calling extra attention to featured content or information.</p>
      <p><Button variant="primary">Learn more</Button></p>
    </Jumbotron>
  )
}
function ShoeCard(props) {
  return (
    <Card style={{ width: '18rem' }}>
      <Card.Img variant="top" src={'https://codingapple1.github.io/shop/shoes' + (props.idx + 1) + '.jpg'} />
      <Card.Body>
        <Card.Title>{props.shoe.title}</Card.Title>
        <Card.Text>{props.shoe.content}</Card.Text>
        <Card.Text>{props.shoe.price}</Card.Text>
      </Card.Body>
    </Card>
  )
}

export default App;
